import React, {useState, useEffect} from "react";
import Table from "react-bootstrap/Table";
import "bootstrap/dist/css/bootstrap.min.css";
import "./../App.css"

function PersonalBest({ personalBestTable, setPersonalBestTable }) {
  const [sortedBestTable, setSortedBestTable] = useState([]);

  useEffect(() => {
    var byWPM = personalBestTable.slice(0);
    byWPM.sort(function(a,b) {
      return b.WPM - a.WPM;
    });
    setSortedBestTable(byWPM.slice(0, 3));
  },[]);

  return (
		<div className="tableContainer">
      <h1>John's Personal Best Times:</h1>
    <Table className="tableItself" hover striped bordered variant="dark">
      <thead>
        <tr>
          <th>#</th>
          <th>Text</th>
          <th>WPM</th>
          <th>Accuracy</th>
        </tr>
      </thead>
      <tbody>
        {sortedBestTable.map((item, index) => (
          <tr>
            <td>{index+1}</td>
						<td className="textTable">{item.Text}</td>
						<td>{item.WPM}</td>
						<td>{(((item.Text.length-item.mistakes)/item.Text.length)*100).toFixed(1)}%</td>
          </tr>
        ))}
      </tbody>
    </Table></div>
  );
}

export default PersonalBest;
