import React, { useState, useEffect } from "react";
import Letter from "./Letter";
import useKeyPress from "../hooks/useKeyPress";
import "./DisplayLetters.css";
import { currentTime } from "./time";

const DisplayLetters = ({ letters, setLetters, setCompleted, newText, personalBestTable, setPersonalBestTable, sortPBs }) => {
  const [arrFromLet, setArrFromLet] = useState(Array.from([...letters]));
  const [uncompletedChars, setUncompletedChars] = useState([]);
  const [completedChars, setCompletedChars] = useState([]);
  const [currentChar, setCurrentChar] = useState([]);
  const [speed, setSpeed] = useState(0);
  const [mistakes, setMistakes] = useState(0);
  const [targetLength, setTargetLength] = useState(arrFromLet.length);
 

  const [startTime, setStartTime] = useState();
  const [wordCount, setWordCount] = useState(0);

  useEffect(() => {
    setCurrentChar(arrFromLet[0]);
    arrFromLet.shift();
    setUncompletedChars(arrFromLet);
  }, []);

  useEffect(() => {
    setArrFromLet(Array.from([...letters]));

  }, [letters])

  const getKey = () => {
    return personalBestTable.length;
  }

  useKeyPress((key) => {
    if (!startTime) {
      setStartTime(currentTime());
    }
    let uncompletedCharsLet = uncompletedChars;

    if (key === currentChar) {
      const durationInMinutes = (currentTime() - startTime) / 60000.0;
      if (uncompletedCharsLet[0] === " ") {
        //4
        setWordCount(wordCount + 1);
        //5
        
        //6
        setSpeed(((wordCount + 1) / durationInMinutes).toFixed(2));
      }
      setCompletedChars([...completedChars, currentChar]);
      setCurrentChar(uncompletedChars[0]);
      uncompletedCharsLet.shift();
      setUncompletedChars(uncompletedCharsLet);
      
      if(completedChars.length+1 === targetLength){
        
        setCompleted(true);
        const finalTime = parseFloat(((wordCount + 1) / durationInMinutes).toFixed(2)); //WPM
        const finalMistakes = mistakes;
        newText();

        setPersonalBestTable([...personalBestTable, {Key:getKey(), Text: letters, WPM: finalTime, mistakes: finalMistakes}])
        
        // setUncompletedChars([]);
        // setCompletedChars([]);
        // setCurrentChar([]);
      }
    } else {
        setMistakes(mistakes+1);
    }
  });

  return (
    <div className="container">
      <div className="indicatorRow">
        <span className="speed">Speed: {speed}</span>
        <span className="mistakes">Errors: {mistakes}</span>
      </div>
      <hr />
      <div className="writingArea">
  
        {completedChars.map((letter) => (
          <Letter letter={letter} completion={true} current={false} />
        ))}

        <Letter letter={currentChar} completion={false} current={true} />

        {uncompletedChars.map((letter) => (
          <Letter letter={letter} completion={false} current={false} />
        ))}
      </div>
    </div>
  );
};

export default DisplayLetters;
