import React, { Component, useState } from "react";
import "./App.css";
import Navbar from "./components/Navbar";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Home from "./pages/Home";
import Account from "./pages/Account";
import SortTheWords from "./pages/SortTheWords";

import { config } from "./config";

import firebase from "firebase";

import {
  FirebaseAuthProvider,
  FirebaseAuthConsumer,
  IfFirebaseAuthed,
  IfFirebaseAuthedAnd,
} from "@react-firebase/auth";

!firebase.apps.length ? firebase.initializeApp(config) : firebase.app();

class App extends Component {
  state = { isSignedIn: false };
  uiConfig = {
    signInFlow: "popup",
    signInOptions: [
      firebase.auth.GoogleAuthProvider.PROVIDER_ID,
      firebase.auth.GithubAuthProvider.PROVIDER_ID,
      firebase.auth.EmailAuthProvider.PROVIDER_ID,
    ],
    callbacks: {
      signInSuccessWithAuthResult: () => false,
    },
  };

  componentDidMount = () => {
    firebase.auth().onAuthStateChanged((user) => {
      this.setState({ isSignedIn: !!user });
    });
  };

  render() {
    return (
      <FirebaseAuthProvider firebase={firebase} {...config}>
        <div className="Body">
          <div className="Body-header">HEADER</div>
          <div className="Body-container">
            <Router>
              <aside className="Body-aside">ASIDE</aside>
              <main id="App" className="Body-app">
                <Switch>
                  <Route path="/account">
                    <Account uiConfig={this.uiConfig} isSignedIn={this.state.isSignedIn} />
                  </Route>
                  <Route path="/sort">
                    <SortTheWords />
                  </Route>
                  <Route path="/catchyname" exact>
                    <Home />
                  </Route>
                </Switch>
              </main>
              <nav id="Nav" className="Body-nav">
                <Navbar />
              </nav>
            </Router>
          </div>

          <footer className="Body-footer">
            Copyright &copy; {new Date().getFullYear()} | Author: Ján Kašpárek
          </footer>
        </div>
      </FirebaseAuthProvider>
    );
  }
}

export default App;
