import React, { useState, useEffect } from "react";
import "../components/SortTheWords.css";
import Button from "react-bootstrap/Button";
import ListGroup from "react-bootstrap/ListGroup";

function SortTheWords() {
  const [data, setData] = useState(require("../wordlist/slovak-words.json"));
  const [stats, setStats] = useState({
    wordsSorted: 0,
    wordsSkipped: 0,
    wordsRefused: 0,
  });
  const [allreadySorted, setAllreadySorted] = useState([]);
  const [activeWord, setActiveWord] = useState("test");

  function generateNewWord() {
    do {
      var random = Math.floor(Math.random() * 1670705);
    } while (allreadySorted.includes(random));
    setAllreadySorted([...allreadySorted, random]);
    return data.SK[random];
  }

  function wordHandler() {
    setActiveWord(generateNewWord());
	}
	
  function skip() {
		setActiveWord(generateNewWord());
		setStats({...stats, wordsSkipped: stats.wordsSkipped + 1})
  }

	function alertClicked() {
		alert('You clicked ListGroupItem');
	}

  useEffect(() => {
    wordHandler();
	}, []);

  return (
    <div className="container">
      <div className="indicatorRow">
        <span className="sorted">Sorted: {stats.wordsSorted}</span>
        <span className="skipped">Skipped: {stats.wordsSkipped}</span>
        <span className="refused">Refused: {stats.wordsRefused}</span>
				<span className="buttonSkip">				<Button variant="danger" onClick={skip}>
        Skip
      </Button>
			</span>
      </div>
      <hr />
      <div className="activeWord">{activeWord}</div>
			<div className="buttonsContainer">
			 	<div className="column one">

      <ListGroup defaultActiveKey="#link1">
        <ListGroup.Item href="#link1" action onClick={alertClicked}>
          Podstatné meno
        </ListGroup.Item>
        <ListGroup.Item href="#link2" action onClick={alertClicked}>
          Prídavné meno
        </ListGroup.Item>
				<ListGroup.Item href="#link2" action onClick={alertClicked}>
          Zámeno
        </ListGroup.Item>
				<ListGroup.Item href="#link2" action onClick={alertClicked}>
          Číslovka
        </ListGroup.Item>
				<ListGroup.Item href="#link2" action onClick={alertClicked}>
          Sloveso
        </ListGroup.Item>
				<ListGroup.Item href="#link2" action onClick={alertClicked}>
          Príslovka
        </ListGroup.Item>
				<ListGroup.Item href="#link2" action onClick={alertClicked}>
          Predložka
        </ListGroup.Item>
				<ListGroup.Item href="#link2" action onClick={alertClicked}>
          Spojka
        </ListGroup.Item>
				<ListGroup.Item href="#link2" action onClick={alertClicked}>
          Častica
        </ListGroup.Item>
				<ListGroup.Item href="#link2" action onClick={alertClicked}>
          Citoslovce
        </ListGroup.Item>
      </ListGroup>

			</div>
			 	<div className="column two"></div>
			 	<div className="column three"></div>
			 	<div className="column four"></div>
			 	<div className="column five"></div>
			</div>


      
    </div>
  );
}

export default SortTheWords;
