import "./../App.css";
import React from "react";
import StyledFirebaseAuth from "react-firebaseui/StyledFirebaseAuth";
import firebase from "firebase";
import Button from 'react-bootstrap/Button';
import 'bootstrap/dist/css/bootstrap.min.css';
import PersonalBest from "../components/PersonalBest";


function Account({ uiConfig, isSignedIn }) {
  return (
    <>
      {/* <PersonalBest personalBestTable={personalBestTable} setPersonalBestTable={setPersonalBestTable} key={personalBestTable} /> */}
      {isSignedIn ? (
        <div>
          <h1>Welcome {firebase.auth().currentUser.displayName}!</h1>
          <Button variant="danger" onClick={() => firebase.auth().signOut()}>Sign out!</Button>
        </div>
      ) : (
        <StyledFirebaseAuth
          uiConfig={uiConfig}
          firebaseAuth={firebase.auth()}
        />
      )}
    </>
  );
}

export default Account;
