import React, { useState, useEffect } from "react";
import SimpleKeyboard from "../components/SimpleKeyboard";
import "../components/Keyboard.css";
import DisplayLetters from "../components/DisplayLetters";
import PersonalBest from "../components/PersonalBest";


function Home() {
  const [data, setData] = useState(require('../wordlist/slovak-best.json'));
  const [letters, setLetters] = useState(generateFullRandom()); //Toto je test. Tento text bude automaticky generovaný. max 90 chars
  const [completed, setCompleted] = useState(false);
  const [personalBestTable, setPersonalBestTable] = useState([]);
  
  function newText() {
    setLetters(generateFullRandom());
    setCompleted(false);
  }

  function generateFullRandom() {
    //console.log(data)
    var finalString = "";
    var newWord = "";
    var randomNumber = Math.floor(Math.random() * 26378);
    finalString = data.SK[randomNumber];
    do{
      randomNumber = Math.floor(Math.random() * 26378);
      newWord = data.SK[randomNumber];
      finalString = finalString + " " + newWord;
    }while(finalString.length<= 90)
    return finalString;
  }

  useEffect(() => {
    getPB();
    generateFullRandom();
  }, []);

  useEffect(() => {
    savePB();
  }, [personalBestTable]);

  const savePB = () => {
    localStorage.setItem("PersonalBest", JSON.stringify(personalBestTable))
  }

  const getPB = () => {
    if(localStorage.getItem("PersonalBest") === null){
      localStorage.setItem("PersonalBest", JSON.stringify([])) 
    } else {
      let PBLocal = JSON.parse(localStorage.getItem("PersonalBest"));
      setPersonalBestTable(PBLocal);
    }
  }

  return (
    <>
      <div className="simplekeyboard">
        <DisplayLetters
          letters={letters}
          setCompleted={setCompleted}
          newText={newText}
          key={letters}
          personalBestTable={personalBestTable}
          setPersonalBestTable={setPersonalBestTable}
          
        />
        <SimpleKeyboard />
        <PersonalBest personalBestTable={personalBestTable} setPersonalBestTable={setPersonalBestTable} key={personalBestTable} />
      </div>
    </>
  );
}

export default Home;
